body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to right, rgb(14, 51, 151), rgb(0, 150, 170), rgb(14, 51, 151));
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main {
  /* Make sure main content clears the toolbar and footer */
  padding-top: 56px;
  padding-bottom: 56px;
}

.centered-message {
  padding-top: 50px;
  text-align: center;
}

.scroll-up {
 height: 600px;	
 overflow: hidden;
 position: relative;
}
.scroll-up div {
 position: absolute;
 width: 100%;
 height: 100%;
 margin: 0;
 line-height: 75px;
 text-align: center;
 word-wrap: normal;
 /* Starting position */
 -webkit-transform:translateY(100%);	
 transform:translateY(100%);
 /* Apply animation to this element */
 -webkit-animation: scroll-up 20s linear infinite;
 animation: scroll-up 20s linear infinite;
}
/* Move it (define the animation) */
@-webkit-keyframes scroll-up {
 0%   { -webkit-transform: translateY(100%); }
 100% { -webkit-transform: translateY(-100%); }
}
@keyframes scroll-up {
 0%   { /* Firefox bug fix */
  -webkit-transform: translateY(100%); /* Firefox bug fix */
  transform: translateY(100%);
 }
 100% { /* Firefox bug fix */
  -webkit-transform: translateY(-100%); /* Firefox bug fix */
  transform: translateY(-100%);
 }
}

.toggle-button {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column; /* display 3 bars below each other */
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 24px;
    width: 30px;
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
}

.toggle-button:focus {
    outline: none;
}

.toggle-button__line {
    width: 30px;
    height: 2px;
    background: white;
}
.toolbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: rgb(14, 11, 160);
    height: 56px;
    z-index: 10;
    box-shadow: 0 2px 4px 0 rgba( 0, 0, 0, .8 );
}

.toolbar__navigation {
    display: -webkit-flex;
    display: flex;
    height: 100%;       /* so vertical center tracks the toolbar height */
    -webkit-align-items: center;
            align-items: center;
    padding: 0 1rem;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}

.toolbar__logo {
    margin-left: 1rem;
}

.toolbar__logo a {
    color: white;
    text-decoration: none;
    font-size: 1.5rem;
}

.toolbar__navigation-items {
    margin-left: auto;
    font-size: 1.2rem;
}

.toolbar__navigation-items ul {
    list-style: none;   /* remove bullets */
    margin: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;      /* line up list items horizontally */
}

.toolbar__navigation-items li {
    padding: 0 0.5rem;
}

.toolbar__navigation-items a {
    color: white;
    text-decoration: none;
}

.toolbar__logo a:hover,
.toolbar__logo a:active,
.toolbar__navigation-items a:hover,
.toolbar__navigation-items a:active {
    color: yellow;
}

@media (max-width: 750px) {
    .toolbar__navigation-items {
        display: none;
    }
    .toolbar__logo {
        margin-left: auto;
        margin-right: auto;
    }
}

@media (min-width: 751px) {
    .toolbar__toggle-button {
         display: none;
    }
    .toolbar__logo {
        margin-left: 0;
    }
}
.side-drawer {
    height: 100%;
    background: rgb(14, 11, 160);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;   /* above the backdrop */
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.side-drawer.open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    box-shadow: 3px 0 10px rgba(0,0,0,0.7);
}

.side-drawer ul {
    height: 100%;
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-padding-start: 20px;
            padding-inline-start: 20px;   
    -webkit-padding-end: 20px;   
            padding-inline-end: 20px;   
}

/* .side-drawer li {
    margin: 0.5rem 0;
} */

.side-drawer a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.side-drawer a:hover,
.side-drawer a:active {
    /* font-weight: bold; */
    color: yellow;
}

@media (min-width: 751px) {
    .side-drawer {
        display: none;
    }
}
.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    z-index: 100;
}
.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    border-top: 1px solid grey;
    background: white;
    color: grey;
    text-align: center;
    font-size: 0.7rem;
}

.card {
    text-align: center;
    background: #222222;
    color: #cccccc;
    display: inline-block;
    border-radius: .5rem;
    padding: 1rem;
    margin: 0.5rem;
    border-width: 0.25rem;
    box-shadow: 4px 4px 8px 0 rgba( 0, 0, 0, .8 );
    text-decoration: none;
}

/* Can combine this with overflow-hidden to make background images grow on hover
 * even if you are using background-size: cover */
.grow {
    -moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translateZ( 0 );
    transform: translateZ( 0 );
    transition: -webkit-transform .25s ease-out;
    transition: transform .25s ease-out,
    -webkit-transform .25s ease-out; }
.grow:hover, .grow:focus {
    -webkit-transform: scale( 1.05 );
    transform: scale( 1.05 );
    box-shadow: 6px 6px 12px 0 rgba( 0, 0, 0, .6 );
 }
.grow:active {
    -webkit-transform: scale( .90 );
    transform: scale( .90 );
    box-shadow: 3px 3px 6px 0 rgba( 0, 0, 0, .8 );
}

.card h2 {
    border-bottom: 2px solid gray;
}

.card-list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.category-titlebar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 20px 0 10px;
}

.detail {
    text-align: center;
    color: white;
}

.detail pre {
    font-family: inherit;
}

.detail .list-title,
.detail h1 {
    /* border: 1px solid; */
    width: 320px;
    margin: 20px auto 5px;
    padding: 4px 10px;
    background: rgb(14, 11, 160);
    box-shadow: 1px 1px 4px 0 rgba( 0, 0, 0, .8 );
}

.detail h1 {
    width: inherit;
    /* Go to top under nav so pulldown on iPhone looks ok */
    padding: 80px 0 8px;
    margin-top: -60px;
}

.detail h2,
.detail h3,
.detail h4 {
    padding-left: 20px;
    padding-right: 20px;
}

.detail a {
    color: #eeeeee;
}

